@import "@theme/vars.scss";

button,
.button,
a {
    cursor: pointer;
    padding: 0;
    border: 0;
    outline: 0;
    background: none;
}

.button {
    box-sizing: border-box;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    padding: 0 24px;
    height: 50px;
    border-radius: 32px;
    font-family: 'PlayfairDisplay';
    text-decoration: none;
    font-size: 18px;
    font-weight: 500;
    background: linear-gradient(36.29deg, rgba(226, 162, 105, 0.68) -27.72%, #F6D1A5 240.25%, #9E5F35 513.85%);
    color: #12141D;
    fill: #12141D;
    box-shadow: 0px 4px 48px rgba(233, 189, 139, 0.26);
    transition: color 0.35s ease, fill 0.35s ease, border-color 0.35s ease, background-color 0.35s ease, opacity 0.35s ease, box-shadow 0.35s ease;

    @media only screen and (max-width: $bp-md) {
        height: 50px;
    }
        
    &:hover{
        box-shadow: 0px 4px 48px rgba(233, 189, 139, 0.13);
    }

    &--md{
        height: 42px;
        font-size: 16px;
        padding: 0 22px;
    }

    &__text{
        
    }

    &__icon{
        flex-shrink: 0;
        width: 30px;
        height: 30px;
    }

    &__text + &__icon{
        margin-left: 10px;
    }

    &__icon + &__text{
        margin-left: 10px;
    }

    &[disabled]{
        pointer-events: none;
        opacity: 0.5;
    }

    &--icon{
        padding: 0;
        width: 54px;
    }
}