@keyframes fadeIn {
    0% {
        opacity: 0;
    }

    100% {
        opacity: 1;
    }
}

@keyframes fadeInTop {

    0% {
        opacity: 0;
        transform: translateY(-15px);
    }
  
    100% {
        opacity: 1;
        transform: translateY(0);
    }
}
  
@keyframes fadeOut {
    0% {
        opacity: 0;
    }

    100% {
        opacity: 0;
    }
}

@keyframes rotate {
    0%{
        rotate: 0deg;
    }

    100%{
        rotate: 360deg;
    }
}

@keyframes rotateReverse {
    0%{
        rotate: 360deg;
    }

    100%{
        rotate: 0deg;
    }
}


// Preloader

@keyframes scaleToggle {
    0%, 100%{
        transform: scale(1);
    }

    50%{
        transform: scale(0.95);
    }
}


// Start

@keyframes startBlinking {
    0%, 100%{
        opacity: 0.4;
    }

    50%{
        opacity: 1;
    }
}

@keyframes startSatellites {
    0%{
        rotate: 0deg;
    }

    100%{
        rotate: 360deg;
    }
}

@keyframes startSatellitesReverse {
    0%{
        rotate: 375deg;
    }

    100%{
        rotate: 15deg;
    }
}

@keyframes startCore{
    0%{
        rotate: 0deg;
    }

    100%{
        rotate: 360deg;
    }
}



