@import "@theme/vars.scss";

.s-intro {
    position: relative;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 120px 0;
    min-height: 100vh;
    margin-bottom: -20vh;
   
    @media only screen and (min-height: 2160px) {
        min-height: 50vh;
        margin-bottom: 0;
    }

    @media only screen and (min-height: 1440px) {
        min-height: 70vh;
        margin-bottom: -10vh;
    }

    @media only screen and (max-width: $bp-lg) {
        padding: 100px 0;
    }

    @media only screen and (max-width: $bp-lg) {
        padding: 80px 0;
    }

    &__bg{
        position: absolute;
        top: 0;
        right: 0;
        pointer-events: none;
        display: flex;
        width: 100%;
        height: 100%;

        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
            object-position: center top;

            @media only screen and (max-width: $bp-md) {
                object-position: right top;
                object-fit: contain;
            }
        }
    }

    &__inner {
        position: relative;
        display: flex;
        align-items: center;

        @media only screen and (max-width: $bp-md) {
            flex-direction: column;
        }
    }

    &__main {
        width: 100%;
        max-width: 480px;
        margin-right: 120px;

        @media only screen and (max-width: $bp-xl) {
            margin-right: 80px;
        }

        @media only screen and (max-width: $bp-lg) {
            margin-right: 45px;
        }

        @media only screen and (max-width: $bp-md) {
            margin-right: 0;
        }
    }

    &__title {
        margin-bottom: 20px;
        background: linear-gradient(53.18deg, #E2A269 -26.25%, #F6D1A5 46.91%, #9E5F35 121.61%);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        background-clip: text;
        text-fill-color: transparent;
    }

    &__text {
        margin-bottom: 10px;

        p{
            font-weight: 200;
        }

        a{
            background: $gold;
            -webkit-background-clip: text;
            -webkit-text-fill-color: transparent;
            background-clip: text;
            text-fill-color: transparent;
            background-size: 100% 100%;
            background-position: center;
            color: transparent;
            text-decoration: none;
            transition: background-size 0.35s ease, opacity 0.35s ease;
        
            &:hover{
                background-size: 150% 100%;
                opacity: 0.8;
            }
        }
    }

    &__img {
        width: 356px;
        height: 360px;
        flex-shrink: 0;
        border-radius: 50%;
        background-color: rgba(255, 255, 255, 0.03);
        overflow: hidden;
        margin-bottom: auto;

        @media only screen and (max-width: $bp-md) {
            order: -1;
            margin-bottom: 32px;
            width: 258px;
            height: 266px;
        }

        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }
}