@import "@theme/vars.scss";

.s-form {
    box-sizing: border-box;
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    min-height: 80vh;
    padding: 55px 0 110px;
    overflow: visible;
    
    @media only screen and (min-width: 2560px) {
        min-height: 40vh;
    }

    .container{
        @media only screen and (max-width: $bp-md) {
            padding: 0 24px;
        }
    }

    &__main {
        box-sizing: border-box;
        width: 100%;
        max-width: 912px;
        position: relative;
        border-radius: 40px;
        margin: 0 auto;
        padding: 1px;
        background: linear-gradient(342.5deg, rgba(226, 162, 105, 0) -6.65%, rgba(246, 209, 165, 0.59) 74.32%, rgba(158, 95, 53, 0.59) 99.85%);
        transition: box-shadow 0.85s ease;

        @media only screen and (max-width: $bp-lg) {
            border-radius: 30px;
        }

        @media only screen and (max-width: $bp-md) {
            border-radius: 20px;
			background: 0;
        }

        &::before{
            content: "";
            display: block;
            width: 795px;
            height: 435px;
            position: absolute;
            top: 0;
            left: 50%;
            z-index: -1;
            transform: translateX(-50%);
            background: linear-gradient(342.4deg, rgba(116, 81, 66, 0.9) 6.91%, #F6D1A5 72.54%, #9E5F35 93.23%);
            opacity: 0;
            filter: blur(82px);
            transition: opacity 0.85s ease;

            @media only screen and (max-width: $bp-md) {
                content: none;
            }
        }
    }

    &__inner{
        box-sizing: border-box;
        width: 100%;
        padding: 40px;
        background-color: $dark;
        border-radius: inherit;

        @media only screen and (max-width: $bp-lg) {
            padding: 36px 30px;
        }

        @media only screen and (max-width: $bp-md) {
            padding: 30px 25px;
			background-color: transparent;
        }
    }

	&__inner{
		display: flex;
		align-items: flex-start;

		@media only screen and (max-width: $bp-md) {
			align-items: center;
			flex-direction: column;
		}
	}

	&__form{
		flex-grow: 1;

		@media only screen and (max-width: $bp-md) {
			width: 100%;
		}
	}

	&__frame{
		position: relative;
		flex-shrink: 0;
		border-radius: 50%;
		overflow: hidden;
		width: 100%;
		max-width: 200px;
		aspect-ratio: 1;
		margin-right: 25px;
		transition: box-shadow 0.75s ease;
		
		@media only screen and (max-width: $bp-md) {
            max-width: 212px;
			margin: 0 0 20px;
		}

        @media only screen and (max-width: $bp-sm) {
			max-width: 256px;
        }
	}

	&__frame-border{
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		object-fit: contain;
		z-index: 1;
	}

	&__frame-video{
		width: 100%;
		height: 100%;
		object-fit: cover;
	}

	&__grid{
		display: flex;
		flex-direction: column;
		margin-top: 28px;

		@media only screen and (max-width: $bp-md) {
			margin-top: 0;
		}
		
		&>*:first-child{
			margin-top: 0;
		}
	}

	&__grid-row{
		display: flex;
		justify-content: flex-end;
		flex-wrap: wrap;
		align-items: center;
		margin: 0 -12.5px -8px;

		@media only screen and (max-width: $bp-md) {
			margin-bottom: 0;
		}

		&:last-child{
			margin-bottom: -32px;

			@media only screen and (max-width: $bp-md) {
				margin-bottom: -15px;
			}
		}
	}

	&__grid-col{
		box-sizing: border-box;
		display: flex;
		flex-direction: column;
		padding: 0 12.5px;
		flex: 1 1 50%;

		@media only screen and (max-width: $bp-sm) {
			flex-basis: 100%;
		}

		&>*{
			margin-bottom: 32px;

			@media only screen and (max-width: $bp-md) {
				margin-bottom: 15px;
			}
		}
	}

	&__input{
	}

	&__textarea{
		.input__field{
			height: 150px;

			@media only screen and (max-width: $bp-md) {
				height: 120px;
			}
		}
	}

	&__button{
		margin-left: auto;

		@media only screen and (max-width: $bp-md) {
			margin-right: auto;
			margin-left: 0;
		}

        @media only screen and (max-width: $bp-sm) {
            flex-grow: 1;
            margin-right: 0;
        }
	}

    
    &__text{
        position: absolute;
        bottom: 45px;
        left: 50%;
        transform: translateX(-50%);
        color: #DDDDDD;
        opacity: 0.5;

        @media only screen and (max-width: $bp-md) {
            bottom: 30px;
        }
    }
}


body.is-loaded {
	.s-form__main.is-animate {
		&::before{
			opacity: 0.22;
		}

		.s-form__frame{
			@media only screen and (max-width: $bp-md) {
				box-shadow: 0px 4px 51px rgba(253, 209, 159, 0.19);
			}
		}
	}
}
