@import '@theme/vars.scss';

@import "~bootstrap/scss/functions";
@import "~bootstrap/scss/variables";
@import "~bootstrap/scss/mixins";
@import "~bootstrap/scss/modal";
@import "~bootstrap/scss/transitions";

.fade{
	transition: opacity 0.25s linear;
}

.modal {
    --bs-modal-zindex: 50;

	&.fade &-dialog{
        transform: none !important;
	}

	&.fade &-content{
		transform: scale(103%);
        opacity: 0;
		will-change: transform;
		transition: transform 0.45s ease-out, opacity 0.45s ease;

	}

	&.show &-content{
        opacity: 1;
		transform: none;
	}

	&-backdrop{
        --bs-backdrop-zindex: 45;
		background: rgba(28, 30, 39, 0.45);
        opacity: 0;
        backdrop-filter: blur(3px);

		&.show{
			opacity: 1;
		}
	}

	&-dialog {
		border-radius: 0;
		border: none;
		max-width: 925px;
		margin-right: auto;
		margin-left: auto;

        @media only screen and (max-width: $bp-lg) {
            padding: 0 30px;
        }
	}

	&-content {
		box-sizing: border-box;
		display: flex;
		border: none;
		border-radius: 40px;
		padding: 1px;
        box-shadow: 0px 15px 52px rgba(246, 209, 165, 0.29);
		background: linear-gradient(342.5deg, rgba(226, 162, 105, 0) -6.65%, rgba(246, 209, 165, 0.59) 74.32%, rgba(158, 95, 53, 0.59) 99.85%);
	
		@media only screen and (max-width: $bp-lg) {
			border-radius: 30px;
		}
		
		@media only screen and (max-width: $bp-md) {
			border-radius: 20px;
		}
	}

	&-close {
		display: flex;
		align-items: center;
		justify-content: center;
		width: 24px;
		height: 24px;
		background-color: transparent;
		display: flex;
		align-items: center;
		justify-content: center;
		border: none;
		cursor: pointer;
        margin-left: auto;

		.icon {
			width: 100%;
			height: 100%;
			transition: opacity 0.35s ease;
		}

		&:hover .icon{
			opacity: 0.8;
		}
	}

	&__main{
		box-sizing: border-box;
		display: flex;
		flex-direction: column;
		width: 100%;
		padding: 42px 48px;
		border-radius: inherit;
		background-color: $dark;

		@media only screen and (max-width: $bp-lg) {
			padding: 32px 24px;
		}

		@media only screen and (max-width: $bp-md) {
			padding: 20px 25px 30px;
		}
	}

    &__header{
        display: flex;
        align-items: center;
		justify-content: space-between;
		margin-bottom: 20px;
        
        &:last-child{
            margin-bottom: 0;
        }
    }

	&__title{
		background: $gold;
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        background-clip: text;
        text-fill-color: transparent;
        color: transparent;
	}

	&__subtitle{
		// font-size: 20px;
		// line-height: 160%;
		margin-bottom: 20px;
		background: $gold;
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        background-clip: text;
        text-fill-color: transparent;
        color: transparent;

		// @media only screen and (max-width: $bp-lg) {
		// 	font-size: 18px;
		// }
	}

	&__body{
		display: flex;
		flex-wrap: wrap;
		align-items: flex-start;

		@media only screen and (max-width: $bp-lg) {
			align-items: center;
			flex-direction: column;
		}
	}

	&__form{
		flex-grow: 1;

		@media only screen and (max-width: $bp-lg) {
			width: 100%;
		}
	}

	&__frame{
		position: relative;
		flex-shrink: 0;
		border-radius: 50%;
		overflow: hidden;
		width: 100%;
		max-width: 200px;
		aspect-ratio: 1;
		margin-right: 25px;
		
		@media only screen and (max-width: $bp-lg) {
			max-width: 256px;
			margin: 0 0 20px;
		}
	}

	&__frame-border{
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		object-fit: contain;
		z-index: 1;
	}

	&__frame-video{
		width: 100%;
		height: 100%;
		object-fit: cover;
	}

	&__grid{
		display: flex;
		flex-direction: column;
		margin-top: 28px;

		@media only screen and (max-width: $bp-lg) {
			margin-top: 0;
		}
		
		&>*:first-child{
			margin-top: 0;
		}
	}

	&__grid-row{
		display: flex;
		justify-content: flex-end;
		flex-wrap: wrap;
		align-items: center;
		margin: 0 -12.5px -8px;

		@media only screen and (max-width: $bp-lg) {
			margin-bottom: 0;
		}

		&:last-child{
			margin-bottom: -32px;

			@media only screen and (max-width: $bp-lg) {
				margin-bottom: -25px;
			}

			@media only screen and (max-width: $bp-md) {
				margin-bottom: -15px;
			}
		}
	}

	&__grid-col{
		box-sizing: border-box;
		display: flex;
		flex-direction: column;
		padding: 0 12.5px;
		flex: 1 1 50%;

		@media only screen and (max-width: $bp-sm) {
			flex-basis: 100%;
		}

		&>*{
			margin-bottom: 32px;

			@media only screen and (max-width: $bp-lg) {
				margin-bottom: 25px;
			}

			@media only screen and (max-width: $bp-md) {
				margin-bottom: 15px;
			}
		}
	}

	&__input{
	}

	&__textarea{
		.input__field{
			height: 150px;

			@media only screen and (max-width: $bp-lg) {
				height: 120px;
			}
		}
	}

	&__button{
		margin-left: auto;

		@media only screen and (max-width: $bp-lg) {
			margin-right: auto;
			margin-left: 0;
		}
	}
}