@import "@theme/vars.scss";

.s-project {
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 50px 0;
    min-height: 100vh;


    @media only screen and (min-height: 1440px) {
        min-height: 65vh;
    }

    @media only screen and (min-height: 2160px) {
        min-height: 50vh;
    }

    @media only screen and (max-width: $bp-lg) {
        min-height: 85vh;
    }

    @media only screen and (max-width: $bp-md) {
        min-height: 75vh;
    }

    &__inner {
        position: relative;
        display: flex;
        align-items: center;
        justify-content: space-between;

        @media only screen and (max-width: $bp-md) {
            flex-direction: column;
            justify-content: center;
        }
    }

    &__art{
        pointer-events: none;
        position: relative;
        flex-shrink: 0;

        @media only screen and (max-width: $bp-md) {
            order: -1;
            margin-bottom: 40px;
        }
    }


    &__main{
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        width: 50%;
        max-width: 512px;
        flex-shrink: 0;

        @media only screen and (max-width: $bp-lg) {
            width: 58%;
        }

        @media only screen and (max-width: $bp-md) {
            width: 100%;
            align-items: center;
        }
    }

    &__logo {
        display: flex;
        align-items: center;
        height: 45px;
        max-width: 100%;
        margin-bottom: 30px;
        transition: filter 0.5s ease;

        img{
            max-width: 100%;
            height: 100%;
            object-fit: contain;
        }

        &:hover{
            filter: drop-shadow(0px 4px 12px rgba(253, 209, 159, 0.35));
        }
    }

    &__text {
        width: 100%;
        margin-bottom: 10px;

        // @media only screen and (max-width: $bp-sm) {
        //     font-size: calc(14px + 2 * ((100vw - 360px) / (575 - 360)));
        //     font-size: calc(16 / 576 * 100vw);
        // }

        p{
            margin-bottom: 10px;

            &:last-child{
                margin-bottom: 0;
            }
        }

        a,
        span {
            background: $gold;
            -webkit-background-clip: text;
            -webkit-text-fill-color: transparent;
            background-clip: text;
            text-fill-color: transparent;
            background-size: 100% 100%;
            background-position: center;
            color: transparent;
            text-decoration: none;
            transition: background-size 0.35s ease, opacity 0.35s ease;
            
        }

        a:hover{
            background-size: 150% 100%;
            opacity: 0.8;
        }
    }

    &__links{
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        margin: 20px -12px -16px;

        @media only screen and (max-width: $bp-lg) {
            margin-top: 10px;
        }

        @media only screen and (max-width: $bp-md) {
            margin: 5px -8px -12px;
        }
    }

    &__link{
        box-sizing: border-box;
        display: inline-flex;
        align-items: center;
        max-height: 52px;
        margin: 0 12px 16px;
        transition: filter 0.5s ease;

        @media only screen and (max-width: $bp-md) {
            max-height: 34px;
            margin: 0 8px 12px;
        }

        &:hover{
            filter: drop-shadow(0px 4px 12px rgba(253, 209, 159, 0.35));
        }

        img{
            box-sizing: border-box;
            width: auto;
            height: auto;
            object-fit: contain;
            margin: 0px 0px 0px 0px;
        }

        &--best{
            img{
                height: 54px;
                margin: -10px -14px -16px -14px;

                @media only screen and (max-width: $bp-md) {
                    height: 36px;
                    margin: -6px -8px -12px -8px;
                }
            }
        }

        &--digital{
            img{
                height: 72px;
                margin: -10px -2px -16px -14px;

                @media only screen and (max-width: $bp-md) {
                    height: 48px;
                    margin: -6px 0px -10px -10px;
                }
            }
        }
    }


    &--richness &__inner{
        justify-content: flex-end;
        // min-height: 542px;

        @media only screen and (max-width: $bp-md) {
            min-height: auto;
        }
    }

    &--richness &__art{
        margin-right: 55px;
        width: 480px;
        height: 296px;

        @media only screen and (max-width: $bp-xl) {
            margin-right: 40px;
        }

        @media only screen and (max-width: $bp-lg) {
            width: 400px;
            height: 246px;
        }
        
        @media only screen and (max-width: $bp-md) {
            margin-right: 0;
        }

        @media only screen and (max-width: $bp-sm) {
            width: 308px;
            height: 190px;
        }
    }

    // 
    
    &--aodm &__inner{
        justify-content: flex-start;
    }

    &--aodm &__main{
        max-width: 500px;

        @media only screen and (max-width: $bp-md) {
            max-width: 512px;
        }
    }

    &--aodm &__art{
        width: 512px;
        height: 444px;
        margin-left: 45px;

        @media only screen and (max-width: $bp-xl) {
            width: 480px;
            height: 416px;
            margin-left: 30px;
        }

        @media only screen and (max-width: $bp-lg) {
            width: 420px;
            height: 364px;
        }

        @media only screen and (max-width: $bp-md) {
            width: 380px;
            height: 330px;
            margin-left: 0;
            margin-bottom: 56px;
        }

        @media only screen and (max-width: $bp-sm) {
            width: 285px;
            height: 248px;
        }
    }

    &--aodm &__logo{

        @media only screen and (max-width: $bp-md) {
            height: 34px;
        }
    }

    // 

    &--culture &__inner{
        justify-content: center;

        @media only screen and (max-width: $bp-xl) {
            justify-content: flex-end;
        }
    }

    &--culture &__art{
        width: 500px;
        height: 500px;
        margin-right: 40px;

        @media only screen and (max-width: $bp-xl) {
            width: 480px;
            height: 480px;
        }

        @media only screen and (max-width: $bp-lg) {
            width: 420px;
            height: 420px;
        }

        @media only screen and (max-width: $bp-md) {
            width: 350px;
            height: 350px;
            margin-right: 0;
        }

        @media only screen and (max-width: $bp-sm) {
            width: 280px;
            height: 280px;
        }
    }

    &--culture &__logo{
        @media only screen and (max-width: $bp-md) {
            height: 52px;
        }
    }

    // 

    &--deal &__inner{
        justify-content: flex-start;
    }

    &--deal &__art{
        width: 530px;
        height: 530px;
        margin-left: 70px;

        @media only screen and (max-width: $bp-xl) {
            width: 480px;
            height: 480px;
            margin-left: 60px;
        }
    
        @media only screen and (max-width: $bp-lg) {
            width: 420px;
            height: 420px;
            margin-left: 30px;
        }
    
        @media only screen and (max-width: $bp-md) {
            width: 360px;
            height: 360px;
            margin-left: 0;
        }
    
        @media only screen and (max-width: $bp-sm) {
            width: 280px;
            height: 280px;
        }
    }

    &--deal &__logo{
        @media only screen and (max-width: $bp-md) {
            height: 35px;
        }
    }

    &--deal &__main{
        max-width: 470px;

        @media only screen and (max-width: $bp-md) {
            max-width: 512px;
        }
    }
    
    // 

    &--society &__inner{
        justify-content: flex-end;
    }

    &--society &__art{
        width: 1045px;
        margin-right: -120px;
        margin-top: -55px;

        // @media only screen and (max-width: $bp-xl) {
        //     width: 940px;
        // }
        
        @media only screen and (max-width: $bp-lg) {
            width: 860px;
            margin-top: 0;
        }

        @media only screen and (max-width: $bp-md) {
            margin-right: 0;
            // margin-bottom: 12px;
            width: 650px;
        }

        @media only screen and (max-width: $bp-sm) {
            // transform: translateX(-12%);
            width: 485px;
        }
    }

    &--society &__main{
        z-index: 2;

        @media only screen and (max-width: $bp-xl) {
            max-width: 480px;
        }

        @media only screen and (max-width: $bp-lg) {
            max-width: 420px;
        }
        
        @media only screen and (max-width: $bp-md) {
            max-width: 512px;
        }
    }

    &--society &__logo{
        height: 90px;

        @media only screen and (max-width: $bp-md) {
            height: 72px;
        }
    }
}


.img-richness{
    position: relative;

    &__first{
        display: block;
        object-fit: contain;
        width: 100%;
        height: 100%;
    }

    &__second{
        display: block;
        object-fit: contain;    
        width: 84.4%;
        aspect-ratio: 1;
        position: absolute;
        top: -49%;
        left: -34.4%;
        z-index: 2;
        opacity: 0;
        transition: opacity 1.8s ease;
    }
    

    &__third{
        display: block;
        object-fit: contain;
        width: 30%;
        aspect-ratio: 1;
        position: absolute;
        bottom: 2%;
        right: 11.6%;
        z-index: 2;
        opacity: 0;
        transition: opacity 1.5s ease;
    }
}

.img-aodm{
    position: relative;
    
    &__triangle{
        position: absolute;
        width: 83%;
        height: 82.88%;
        top: 7.8%;
        z-index: 2;
        opacity: 0;
        transition: opacity 0.95s ease 0.6s;
    }

    &__set{
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        opacity: 0;
        transition: opacity 1.1s ease 0.4s;
    }

    &__eclipse{
        position: absolute;
        width: 39%;
        height: 45.04%;
        top: 28.37%;
        left: 42%;
        z-index: 3;
        transition: filter 1.5s ease;
    }

    &__line{
        position: absolute;
        width: 134%;
        height: 1px;
        top: 64.4%;
        left: -26.56%;
        z-index: 4;
        rotate: -30deg;
        opacity: 0;
        transition: opacity 0.95s ease 0.25s;
    }
}

.img-culture{
    &__img{
        width: 100%;
        height: 100%;
        object-fit: contain;
        opacity: 0.15;
        scale: 0.99;
        transition: scale 1.2s cubic-bezier(.74,.38,.56,.97), opacity 1.2s cubic-bezier(.74,.38,.56,.97);
    }
}

.img-deal{
    &__core-group{
        width: 33.5%;
        height: 33.5%;
        position: absolute;
        top: 50%;
        left: 50%;
        translate: -50% -50%;
    }

    &__core-group &__icon{
        position: absolute;
        left: 50%;
        top: 50%;
        width: 50%;
        height: 50%;
        translate: -50% -100%;
        transform-origin: bottom center;
        rotate: 0deg;
        will-change: transform;
        transition: rotate 1.2s cubic-bezier(.63,.17,.3,.97) 0.2s;
        
        &:nth-child(1){
            rotate: -60deg;
        }

        &:nth-child(2){
            rotate: 60deg;
        }

        &:nth-child(3){
            rotate: 180deg;
        }
    }

    &__core-group &__icons{
        position: relative;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;
        height: 100%;

        img{
            display: block;
            width: 110%;
            height: 110%;
            position: absolute;
            border-radius: 50%;
            bottom: 0;
            left: 50%;
            translate: -50% 50%;
            will-change: transform;
            transition: translate 1.4s cubic-bezier(.63,.17,.3,.97) 0.2s;
        }
    }

    &__core{
        display: block;
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        border-radius: 50%;
        transition: box-shadow 1.2s cubic-bezier(.63,.17,.3,.97) 0.1s;
    }

    &__orbit-1{
        display: block;
        position: absolute;
        top: 50%;
        left: 50%;
        translate: -50% -50%;
        width: 41.8%;
        height: 41.8%;
    }

    &__orbit-group{
        position: absolute;
        top: 50%;
        left: 50%;
        translate: -50% -50%;
        width: 70.5%;
        height: 70.5%;
        rotate: 30deg;
        transition: rotate 1.6s cubic-bezier(.63,.17,.3,.97);
    }

    &__orbit-group &__icons{
        width: 100%;
        height: 100%;
        position: relative;

        img{
            position: absolute;
            display: block;
            border-radius: 50%;
            width: 22%;
            height: 22%;
            rotate: -30deg;
            transition: rotate 1.6s cubic-bezier(.63,.17,.3,.97);

            &:nth-child(2){
                top: 50%;
                right: 0;
                translate: 50% -50%;
            }

            &:nth-child(3){
                bottom: 0;
                left: 50%;
                translate: -50% 50%;
            }

            &:nth-child(4){
                top: 50%;
                left: 0;
                translate: -50% -50%;
            }

            &:nth-child(1){
                top: 0;
                left: 50%;
                translate: -50% -50%;
            }
        }
    }
    
    &__orbit-2{
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
    }
    
    &__orbit-3{
        position: absolute;
        top: 50%;
        left: 50%;
        translate: -50% -50%;
        width: 100%;
        height: 100%;
        animation: rotate 55s linear infinite;
    }

}

.img-society{
    position: relative;

    &__first{
        display: block;
        object-fit: contain;
        width: 100%;
    }

    &__second{
        display: block;
        object-fit: contain;
        width: 100%;
        position: absolute;
        top: 0;
        left: 0;
        z-index: 2;
        opacity: 0;
        transition: opacity 1.6s ease-in-out;
    }
}

body.is-loaded .is-animate {
    .img-richness{
        &__second{
            opacity: 1;
            transition-delay: 0.4s;
        }
    
        &__third{
            opacity: 1;
            transition-delay: 0.1s;
        }
    }

    .img-aodm {
        &__triangle{
            opacity: 1;
        }
        
        &__set{
            opacity: 1;
        }
        
        &__eclipse{
            filter: drop-shadow(0px 4px 35px rgba(253, 209, 159, 0.5));
        }
        
        &__line{
            opacity: 1;
        }
    }

    .img-culture {
        &__img{
            opacity: 1;
            scale: 1;
        }
    }

    .img-deal {
        &__core{
            box-shadow: 0px 4.28274px 83.5134px rgba(253, 209, 159, 0.3);
        }
    
        &__core-group .img-deal__icon{
            &:nth-child(1){
                rotate: 60deg;
            }
    
            &:nth-child(2){
                rotate: 180deg;
            }
    
            &:nth-child(3){
                rotate: 300deg;
            }
        }
            
        &__core-group .img-deal__icons img{
            translate: -50% 9%;
        }
    
        &__orbit-group{
            rotate: -30deg;
        }
    
        &__orbit-group .img-deal__icons img{
            rotate: 30deg;
        }
    }

    .img-society {
        &__second{
            opacity: 1;
        }
    }
}
