@import "@theme/vars.scss";
@import "@theme/animations.scss";

.header {
    box-sizing: border-box;
    position: fixed;
    top: 0;
    right: 0;
    z-index: 10;
    width: 100%;
    padding: 30px 0 10px;
    padding-right: inherit;
    background-image: linear-gradient(180deg, #1C1E27 28.38%, rgba(28, 30, 39, 0.33) 147.27%);
    background-repeat: no-repeat;
    background-size: 100%;
    background-position-y: -100px;
    transition: background-position 0.35s ease;

    &.is-scrolled{
        background-position-y: 0;
    }
    
    @media only screen and (max-width: $bp-lg) {
        padding-top: 20px;
    }

    @media only screen and (max-width: $bp-md) {
        padding-top: 10px;
    }

    &__container {
        max-width: none;
        padding: 0 100px;

        @media only screen and (max-width: $bp-xl) {
            padding: 0 50px;
        }

        @media only screen and (max-width: $bp-lg) {
            padding: 0 20px;
        }
    }

    &__inner {
        display: flex;
        align-items: center;
        justify-content: space-between;
    }

    &__logo {
        position: relative;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 60px;
        height: 60px;
        flex-shrink: 0;

        @media only screen and (max-width: $bp-md) {
            width: 45px;
            height: 45px;
        }

        img{
            object-fit: contain;
        }
    }

    &__logo:hover &__logo-core{
        rotate: 90deg;
    }

    &__logo:hover &__logo-frame{
        rotate: -90deg;
    }
    
    &__logo-core{
        display: block;
        width: 28px;
        height: 28px;
        position: absolute;
        top: 50%;
        left: 50%;
        translate: -50% -50%;
        will-change: rotate;
        transition: rotate 0.75s cubic-bezier(.48,.09,.29,.91);

        @media only screen and (max-width: $bp-md) {
            width: 20px;
            height: 20px;
        }
    }

    &__logo-frame{
        width: 100%;
        height: 100%;
        will-change: rotate;
        transition: rotate 0.75s cubic-bezier(.48,.09,.29,.91);
    }

    &__actions {
        display: flex;
        align-items: center;

        &>*{
            margin-left: 15px;
        }
    }

    &__action {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 24px;
        height: 24px;
        flex-shrink: 0;
        opacity: 0.65;
        transition: opacity 0.35s ease;

        .icon{
            width: 100%;
            height: 100%;
        }

        &:hover{
            opacity: 1;
        }
    }
}