.s-start {
    box-sizing: border-box;
    position: relative;
    display: flex;
    flex-direction: column;
    padding: 55px 0;
    height: 100vh;
    min-height: 640px;
    background-color: $dark;
    transition: opacity 0.85s ease, visibility 0.85s ease;

    @media only screen and (max-width: $bp-md) {
        padding: 80px 0;
        min-height: auto;
    }

    @media only screen and (max-width: $bp-sm) {
        overflow: hidden;
    }

    &__bg{
        position: absolute;
        top: 0;
        right: 0;
        pointer-events: none;
        display: flex;
        width: 100%;
        height: 100%;

        @media only screen and (max-width: $bp-md) {
            height: auto;
        }

        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
            object-position: center top;

            @media only screen and (max-width: $bp-md) {
                object-position: center top;
                object-fit: contain;
            }
        }
    }

    &__inner {
        position: relative;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        flex-grow: 1;
    }

    &__main {
        position: relative;
        margin: auto;
        width: 658px;
        height: 555px;
        transform: translateX(-16px);

        // 0.8434650455927052

        @media only screen and (max-width: $bp-md) {
            width: 450px;
            height: 380px;
            transform: translateX(-12px);
        }

        @media only screen and (max-width: $bp-sm) {
            width: 372px;
            height: 314px;
            margin-right: -26px;
            margin-left: -26px;
        }
    }

    // 

    &__shape-line{
        display: block;
        position: absolute;
        top: 270px;
        left: -44px;
        width: 117.6%;
        // width: 774px;
        left: -0.05%;
        height: 1px;
        z-index: 1;
        rotate: -36deg;
        opacity: 0;
        transition: opacity 0.75s ease;
        display: none;

        // 1.17629179331307

        @media only screen and (max-width: $bp-md) {
            top: 185px;
            left: -30px;
            width: 530px;
        }

        @media only screen and (max-width: $bp-sm) {
            width: 438px;
            top: 152px;
            left: -25px;
        }
    }

    &__shape-orbit{
        display: block;
        position: absolute;
        top: 68px;
        left: 52px;
        width: 554px;
        height: 420px;
        z-index: 3;

        @media only screen and (max-width: $bp-md) {
            top: 46px;
            left: 35px;
            width: 378px;
            height: 288px;
        }

        @media only screen and (max-width: $bp-sm) {
            left: 30px;
            top: 38px;
            width: 312px;
            height: 236px;
        }
    }

    &__shape-cores{
        display: block;
        position: absolute;
        top: 122px;
        left: 190px;
        width: 308px;
        height: 308px;
        z-index: 2;
        border-radius: 50%;

        @media only screen and (max-width: $bp-md) {
            top: 84px;
            left: 130px;
            width: 210px;
            height: 210px;
        }

        @media only screen and (max-width: $bp-sm) {
            top: 68px;
            left: 110px;
            width: 175px;
            height: 175px;
        }

        // top: 21.98%;
        // left: 28.87%;
        // width: 46.81%;
        // aspect-ratio: 1;
    }

    &__shape-core{
        display: block;
        position: absolute;
        top: 50%;
        left: 50%;
        width: 45%;
        height: 45%;
        z-index: 2;
        transform: translate(-50%, -50%);
    }

    &__shape-core-border{
        display: block;
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        animation: startCore 35s linear forwards infinite;
    }

    &__shape-satellites{
        position: absolute;
        top: 22px;
        left: 90px;
        width: 508px;
        height: 508px;
        z-index: 4;
        animation: startSatellites 85s linear infinite;

        @media only screen and (max-width: $bp-md) {
            top: 15px;
            left: 62px;
            width: 348px;
            height: 348px;
        }

        @media only screen and (max-width: $bp-sm) {
            top: 12px;
            left: 54px;
            width: 286px;
            height: 286px;
        }
    }

    &__shape-satellites-orbit{
        width: 100%;
        height: 100%;
    }
    
    &__shape-satellites-group{
        img{
            position: absolute;
            display: block;
            width: 40px;
            height: 40px;
            border-radius: 50%;
            box-shadow: 0px 4px 70px #A68068;
            animation: startSatellitesReverse 85s linear infinite;

            @media only screen and (max-width: $bp-md) {
                width: 28px;
                height: 28px;
            }

            @media only screen and (max-width: $bp-sm) {
                width: 24px;
                height: 24px;
            }

            &:nth-child(1){
                top: 0;
                left: 50%;
                translate: -50% -50%;
            }

            &:nth-child(2){
                top: 50%;
                left: 0;
                translate: -50% -50%;
            }

            &:nth-child(3){
                bottom: 0;
                left: 50%;
                translate: -50% 50%;
            }

            &:nth-child(4){
                top: 50%;
                right: 0;
                translate: 50% -50%;
            }
        }
    }

    // 

    &__link {
        position: absolute;
        bottom: 0;
        left: 50%;
        transform: translateX(-50%);
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        margin: 0 auto;
        z-index: 2;
        height: 54px;
        animation: startBlinking 5s ease forwards infinite;

        @media only screen and (max-width: $bp-lg) {
            height: auto;
        }
    }

    &__link:hover &__link-mouse{
        margin: 2px 0;
    }

    &__link-arrow {
        width: 18px;
        height: 8px;

        &--up {
            @media only screen and (max-width: $bp-lg) {
                display: none;
            }
        }
    }

    &__link-mouse {
        width: 18px;
        height: 29px;
        margin: 4px 0;
        transition: margin 0.5s ease;

        @media only screen and (max-width: $bp-lg) {
            display: none;
        }
    }
}

