@import '@theme/vars.scss';

h1, h2, h3, h4, h5, h6, p {
    margin: 0;
    padding: 0;
}

p{
    font-family: 'Montserrat';
    font-weight: 300;
}


.h1, .h2, .h3, .h4, .h5, .h6 {
    font-family: 'PlayfairDisplay';
    font-weight: 600;
}

.h1{
    font-size: 45px;
    line-height: 160%;

    @media only screen and (max-width: $bp-lg) {
        font-size: 35px;
    }

    @media only screen and (max-width: $bp-md) {
        font-size: 30px;
    }
}

.h2{
    font-size: 40px;
    line-height: 160%;

    @media only screen and (max-width: $bp-lg) {
        font-size: 30px;
    }

    @media only screen and (max-width: $bp-md) {
        font-size: 25px;
    }
}

.h3{
    font-size: 35px;
    line-height: 160%;

    @media only screen and (max-width: $bp-lg) {
        font-size: 30px;
    }

    @media only screen and (max-width: $bp-md) {
        font-size: 25px;
    }

}

.h4{
    font-size: 30px;
    line-height: 160%;

    @media only screen and (max-width: $bp-md) {
        font-size: 25px;
    }

    @media only screen and (max-width: $bp-md) {
        font-size: 22px;
    }
    
}

.h5{
    font-size: 25px;
    line-height: 160%;

    @media only screen and (max-width: $bp-lg) {
        font-size: 22px;
    }

    @media only screen and (max-width: $bp-md) {
        font-size: 18px;
    }
}

.h6{
    font-size: 20px;
    line-height: 135%;
}

.text{
    font-size: 18px;
    line-height: 160%;

    @media only screen and (max-width: $bp-lg) {
        font-size: 16px;
    }

    @media only screen and (max-width: $bp-md) {
        font-size: 14px;
        line-height: 170%;
    }

    &-md{
        font-size: 16px;
        line-height: 160%;

        @media only screen and (max-width: $bp-md) {
            font-size: 14px;
            line-height: 170%;
        }
    }

    &-rg{
        font-size: 14px;
        line-height: 170%;

        @media only screen and (max-width: $bp-md) {
            font-size: 12px;
            line-height: 160%;
        }
    }

    &-sm{
        font-size: 12px;
        line-height: 160%;

        @media only screen and (max-width: $bp-md) {
            font-size: 10px;
        }
    }

    //STYLE

    // &-border{
    //     -webkit-text-stroke: 1px #8C8C8C;
    // }

    //WEIGHT

    &-200{
        font-weight: 200;
    }

    &-300{
        font-weight: 300;
    }

    &-400{
        font-weight: 400;
    }
    
    &-500{
        font-weight: 500;
    }
    
    &-600{
        font-weight: 600;
    }

    &-700{
        font-weight: 700;
    }

    //FAMILY

    &-montserrat{
        font-family: 'Montserrat';
    }

    &-playfair{
        font-family: 'Playfair';
    }
}