@import "@theme/vars.scss";

html {
  font-family: $defaultFontFamily;
  font-size: $defaultFontSize;
  line-height: $defaultLineHeight;
  color: $light;
  background-color: $dark;
}

body {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
    overflow-x: hidden;

    &._lock {
        overflow: hidden;
    }
}

*{
    -webkit-tap-highlight-color: transparent;
    scrollbar-color: #EDC69A transparent;
    scrollbar-width: thin;
    
    &::-webkit-scrollbar {
        height: 8px;
        width: 8px;
    }

    &::-webkit-scrollbar-track {
        background-color: transparent;
        border-radius: 5px;
    }

    &::-webkit-scrollbar-thumb {
        background: linear-gradient(144deg, #E2A269 -25%, #F6D1A5 50%, #9E5F35 125%);
        border-radius: 5px;
    }
}

@keyframes gradient{
    0%{
        background-position: 0 0;
    }
}

img{
  image-rendering: -webkit-optimize-contrast;
}

.main{
    flex-grow: 1;
    // overflow: hidden;
    width: 100%;

    section{
        &:first-of-type{
        }

        &:last-child{
        }
    }

    &__group{
        position: sticky;
        bottom: 0;
    }
}

section{
    overflow: hidden;
}

.container {
    box-sizing: border-box;
    width: 100%;
    max-width: 1220px;
    padding: 0 20px;
    margin: 0 auto;

    @media only screen and (max-width: $bp-md) {
        padding: 0 15px;
    }
}