@import "@theme/vars.scss";

.ui{
    padding: 90px 0 60px;
    // background-color: $dark;
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    min-height: 100vh;

    &__inner{

    }

    &__block{
        border-top: 1px solid rgba(201, 201, 201, 0.15);
        padding: 30px 0;

        &:first-child{
            border-top: 0;
        }
    }

    &__row{
        box-sizing: border-box;
        display: flex;
        flex-wrap: wrap;
        margin: 0 -20px -10px;
    }

    &__col{
        box-sizing: border-box;
        position: relative;
        max-width: 100%;
        flex-grow: 1;
        padding: 0 20px;
        margin-bottom: 20px;
        min-height: 50px;
    }

    &__title{
        background: $gold;
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        background-clip: text;
        text-fill-color: transparent;
        color: transparent;
        
    }
}