@import "@theme/vars.scss";

.input{
    box-sizing: border-box;
    position: relative;
    background: none;
    border: none;
    outline: none;
    display: flex;
    align-items: center;
    width: 100%;
    font-weight: 400;
    font-size: 14px;
    line-height: 120%;
    color: $light-dark;
    transition: padding 0.35s ease;

    &__field{
        outline: none;
        box-sizing: border-box;
        width: 100%;
        height: 27px;
        padding: 0 0 10px;
        color: inherit;
        text-overflow: ellipsis;
        background-color: transparent;
        border: 0px solid;
        border-bottom-width: 1px;
        border-color: rgba(201, 201, 201, 0.15);
        transition: background-color 0.35s ease 0.1s, border-color 0.35s ease, color 0.35s ease;

        &:hover{
            border-color: rgba(201, 201, 201, 0.5);
        }

        &::placeholder{
            color: inherit;
            opacity: 0.5;
        }

        &:is(textarea){
            border-radius: 10px;
            padding: 24px 12px;
            border-width: 1px;
            height: 150px;
            resize: none;

            @media only screen and (max-width: $bp-lg) {
                padding: 14px 12px;
            }
        }
    }

    &__error{
        pointer-events: none;
        color: $red;
        font-size: 12px;
        line-height: 14px;
        visibility: hidden;
        opacity: 0;
        position: absolute;
        bottom: 0;
        left: 0;
        max-width: 100%;
        transition: transform 0.35s ease, opacity 0.35s ease, visiblity 0.35s ease;
    }


    &.is-error{
        padding-bottom: 20px;
    }

    &.is-error &__field{
        border-color: $red;
    }

    &.is-error &__error{
        pointer-events: all;
        opacity: 1;
        visibility: visible;
    }

    &.is-filled &__field{
        border-color: $light;
    }

	&.is-focus &__field{
        border-color: $light;
	}
}