@mixin font($name, $weight, $fileName) {
    @font-face {
        font-family: $name;
        src: local('#{$name}'),
            url('@fonts/#{$name}/#{$fileName}.woff2') format('woff2'),
            url('@fonts/#{$name}/#{$fileName}.woff') format('woff'),
            url('@fonts/#{$name}/#{$fileName}.ttf') format('ttf');
        font-weight: $weight;
        font-style: normal;
        font-display: swap;
    }
}
  

// Template

@include font('PlayfairDisplay', 500, 'PlayfairDisplay-Medium');
@include font('PlayfairDisplay', 600, 'PlayfairDisplay-SemiBold');
@include font('Montserrat', 200, 'Montserrat-ExtraLight');
@include font('Montserrat', 300, 'Montserrat-Light');
@include font('Montserrat', 400, 'Montserrat-Regular');
@include font('Montserrat', 500, 'Montserrat-Medium');