@import "@theme/vars.scss";

.sitemap{
    box-sizing: border-box;
    width: 100%;
    min-height: 100vh;
    padding: 20px 0;
    background-color: $dark;

    @media only screen and (max-width: $bp-lg) {
        padding: 12px 0;
    }

    &__title{
        padding: 12px 32px;
        margin-bottom: 12px;
        text-transform: uppercase;
        background-color: $dark;
        z-index: 2;

        @media only screen and (max-width: $bp-lg) {
            padding: 10px 24px;
        }

        @media only screen and (max-width: $bp-md) {
            padding: 8px 20px;
        }

        span{
            background: $gold;
            -webkit-background-clip: text;
            -webkit-text-fill-color: transparent;
            background-clip: text;
            text-fill-color: transparent;
            color: transparent;
        }
    }

    &__nav{
        display: flex;
        flex-direction: column;

        &>*{
            background-color: $dark;

            &:nth-child(odd){
                background-color: #232530;
            }
        }
    }

    &__link{
        display: inline-flex;
        font-weight: 500;
        align-items: center;
        text-transform: capitalize;
        font-size: 18px;
        line-height: 120%;
        padding: 12px 44px;
        color: $light;
        text-decoration: none;
        transition: color 0.35s ease, margin 0.5s ease, background-color 0.5s ease;

        @media only screen and (max-width: $bp-lg) {
            font-size: 16px;
            padding: 10px 36px;
        }

        @media only screen and (max-width: $bp-md) {
            padding-right: 28px;
            padding-left: 28px;
        }
        

        &:hover{
            color: #F6D1A5;
            margin-left: -6px;
        }
    }
}